
"use client";

import * as React from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label"; // Label not directly used, but FormLabel uses it
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card";
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { useAuth } from "@/hooks/use-auth";
import { CalendarIcon, Loader2 } from "lucide-react";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { cn } from "@/lib/utils";
import { format, parseISO, isValid } from 'date-fns';
import { id as localeID } from 'date-fns/locale';
import Image from "next/image";
import { ROLES } from "@/lib/constants";
import { useToast } from "@/hooks/use-toast"; // Import useToast

// Schema for profile updates (Firebase + local DB)
const profileFormSchema = z.object({
  fullName: z.string().min(2, { message: "Nama lengkap minimal 2 karakter." }).optional(),
  phone: z.string().max(20, "Nomor telepon terlalu panjang.").optional().nullable().or(z.literal('')),
  address: z.string().max(500, "Alamat terlalu panjang.").optional().nullable().or(z.literal('')),
  birthDate: z.date().optional().nullable(), 
  bio: z.string().max(300, { message: "Bio maksimal 300 karakter." }).optional().nullable().or(z.literal('')),
  avatarUrl: z.string().url({ message: "URL Avatar tidak valid." }).optional().nullable().or(z.literal('')),
  // Fields specific to roles, managed in local DB via API
  nis: z.string().optional().nullable().or(z.literal('')),
  nip: z.string().optional().nullable().or(z.literal('')),
  kelas: z.string().optional().nullable().or(z.literal('')),
  mataPelajaran: z.string().optional().nullable().or(z.literal('')),
});

type ProfileFormValues = z.infer<typeof profileFormSchema>;

const changePasswordFormSchema = z.object({
  newPassword: z.string().min(6, { message: "Kata sandi baru minimal 6 karakter." }),
  confirmNewPassword: z.string().min(6, { message: "Konfirmasi kata sandi baru minimal 6 karakter." }),
}).refine((data) => data.newPassword === data.confirmNewPassword, {
  message: "Kata sandi baru dan konfirmasi tidak cocok.",
  path: ["confirmNewPassword"],
});

type ChangePasswordFormValues = z.infer<typeof changePasswordFormSchema>;

export default function SettingsPage() {
  const { user: appUser, firebaseUser, updateUserProfile, changePassword, isLoading: authLoading, updateSession } = useAuth();
  const { toast } = useToast();
  const [isProfileSubmitting, setIsProfileSubmitting] = React.useState(false);
  const [isPasswordSubmitting, setIsPasswordSubmitting] = React.useState(false);

  const profileForm = useForm<ProfileFormValues>({
    resolver: zodResolver(profileFormSchema),
  });

  const passwordForm = useForm<ChangePasswordFormValues>({
    resolver: zodResolver(changePasswordFormSchema),
    defaultValues: { newPassword: "", confirmNewPassword: "" }
  });

  React.useEffect(() => {
    if (appUser) { 
      let parsedBirthDate: Date | undefined = undefined;
      if (appUser.birthDate) { 
        const dateCandidate = parseISO(appUser.birthDate);
        if (isValid(dateCandidate)) parsedBirthDate = dateCandidate;
      }
      
      profileForm.reset({
        fullName: appUser.fullName || "",
        phone: appUser.phone || "", 
        address: appUser.address || "", 
        birthDate: parsedBirthDate, 
        bio: appUser.bio || "", 
        avatarUrl: appUser.avatarUrl || "",
        nis: appUser.nis || "",
        nip: appUser.nip || "",
        kelas: appUser.kelas || "",
        mataPelajaran: appUser.mataPelajaran || "",
      });
    }
  }, [appUser, profileForm]);

  async function onProfileSubmit(data: ProfileFormValues) {
    if (!firebaseUser || !appUser) return;
    setIsProfileSubmitting(true);

    const firebaseProfilePayload: { displayName?: string | null; photoURL?: string | null; } = {};
    if (data.fullName !== appUser.fullName) firebaseProfilePayload.displayName = data.fullName || null;
    if (data.avatarUrl !== appUser.avatarUrl) firebaseProfilePayload.photoURL = data.avatarUrl || null;
    
    let firebaseUpdateSuccess = true;
    if (Object.keys(firebaseProfilePayload).length > 0) {
        firebaseUpdateSuccess = await updateUserProfile(firebaseProfilePayload);
    }

    // Prepare local DB update payload
    const localProfilePayload: Partial<User> = {};
    if (data.fullName !== appUser.fullName) localProfilePayload.fullName = data.fullName || undefined;
    if (data.phone !== appUser.phone) localProfilePayload.phone = data.phone || undefined;
    if (data.address !== appUser.address) localProfilePayload.address = data.address || undefined;
    if (data.birthDate && (!appUser.birthDate || format(data.birthDate, 'yyyy-MM-dd') !== appUser.birthDate)) {
      localProfilePayload.birthDate = format(data.birthDate, 'yyyy-MM-dd');
    } else if (!data.birthDate && appUser.birthDate) {
      localProfilePayload.birthDate = null;
    }
    if (data.bio !== appUser.bio) localProfilePayload.bio = data.bio || undefined;
    if (data.avatarUrl !== appUser.avatarUrl) localProfilePayload.avatarUrl = data.avatarUrl || undefined;
    if (data.nis !== appUser.nis && appUser.role === 'siswa') localProfilePayload.nis = data.nis || undefined;
    if (data.nip !== appUser.nip && (appUser.role === 'guru' || appUser.role === 'admin' || appUser.role === 'pimpinan')) localProfilePayload.nip = data.nip || undefined;
    if (data.kelas !== appUser.kelas && appUser.role === 'siswa') localProfilePayload.kelas = data.kelas || undefined;
    if (data.mataPelajaran !== appUser.mataPelajaran && appUser.role === 'guru') localProfilePayload.mataPelajaran = data.mataPelajaran || undefined;


    if (Object.keys(localProfilePayload).length > 0) {
        try {
            const response = await fetch(`/api/users/me/profile`, { // Endpoint to update local user profile
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(localProfilePayload),
            });
            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || "Gagal memperbarui profil lokal.");
            }
            toast({ title: "Profil Lokal Diperbarui", description: "Perubahan pada profil lokal Anda telah disimpan." });
            await updateSession(); // Refresh appUser state from backend
        } catch (error: any) {
            toast({ title: "Error Profil Lokal", description: error.message, variant: "destructive" });
            firebaseUpdateSuccess = false; // Consider overall update failed if local fails
        }
    } else if (!Object.keys(firebaseProfilePayload).length) {
         toast({ title: "Info", description: "Tidak ada perubahan pada profil." });
    }


    setIsProfileSubmitting(false);
  }

  async function onPasswordSubmit(data: ChangePasswordFormValues) {
    setIsPasswordSubmitting(true);
    const success = await changePassword(data.newPassword); 
    if (success) {
      passwordForm.reset();
    }
    setIsPasswordSubmitting(false);
  }
  
  const getInitials = (name?: string | null, email?: string | null) => {
    if (name) {
      const parts = name.split(' ');
      if (parts.length > 1 && parts[0] && parts[parts.length-1]) {
        return `${parts[0][0]}${parts[parts.length-1][0]}`.toUpperCase();
      }
      return name.substring(0,2).toUpperCase();
    }
    if (email) {
      return email.substring(0,2).toUpperCase();
    }
    return '??';
  }

  if (authLoading || !appUser) { 
    return (
      <div className="flex h-full items-center justify-center">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-headline font-semibold">Pengaturan</h1>
      
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle>Informasi Akun</CardTitle>
          <CardDescription>Detail akun Anda saat ini. Beberapa data berasal dari profil lokal Anda.</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex flex-col sm:flex-row items-center space-y-4 sm:space-y-0 sm:space-x-6">
            <Image 
              src={appUser.avatarUrl || `https://placehold.co/100x100.png?text=${getInitials(appUser.fullName || appUser.name, appUser.email)}`} 
              alt={appUser.fullName || appUser.name || "Avatar Pengguna"}
              width={100}
              height={100}
              className="rounded-full object-cover border-2 border-primary"
              data-ai-hint="user avatar"
            />
            <div className="text-center sm:text-left">
              <h2 className="text-2xl font-semibold">{appUser.fullName || appUser.name || "Nama Tidak Ada"}</h2>
              <p className="text-muted-foreground">{appUser.email}</p>
              <p className="text-sm text-primary font-medium">{ROLES[appUser.role]}</p>
            </div>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-x-6 gap-y-2 pt-4 text-sm">
            {appUser.nis && appUser.role === 'siswa' && <p><span className="font-semibold">NIS:</span> {appUser.nis}</p>}
            {appUser.nip && (appUser.role === 'guru' || appUser.role === 'admin' || appUser.role === 'pimpinan') && <p><span className="font-semibold">NIP:</span> {appUser.nip}</p>}
            {appUser.joinDate && isValid(parseISO(appUser.joinDate)) && <p><span className="font-semibold">Tanggal Bergabung:</span> {format(parseISO(appUser.joinDate), 'dd MMMM yyyy', { locale: localeID })}</p>}
            {appUser.phone && <p><span className="font-semibold">Telepon:</span> {appUser.phone}</p>}
            {appUser.address && <p className="md:col-span-2"><span className="font-semibold">Alamat:</span> {appUser.address}</p>}
            {appUser.birthDate && isValid(parseISO(appUser.birthDate)) && <p><span className="font-semibold">Tanggal Lahir:</span> {format(parseISO(appUser.birthDate), 'dd MMMM yyyy', { locale: localeID })}</p>}
            {appUser.bio && <p className="md:col-span-2"><span className="font-semibold">Bio:</span> {appUser.bio}</p>}
            {appUser.kelas && appUser.role === 'siswa' && <p><span className="font-semibold">Kelas:</span> {appUser.kelas}</p>}
            {appUser.mataPelajaran && appUser.role === 'guru' && <p><span className="font-semibold">Mapel Diampu:</span> {appUser.mataPelajaran}</p>}
          </div>
        </CardContent>
      </Card>

      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle>Edit Profil</CardTitle>
          <CardDescription>Perbarui detail pribadi Anda. Perubahan nama dan avatar juga akan disinkronkan ke Firebase.</CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...profileForm}>
            <form onSubmit={profileForm.handleSubmit(onProfileSubmit)} className="space-y-8">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <FormField control={profileForm.control} name="fullName" render={({ field }) => (<FormItem><FormLabel>Nama Lengkap</FormLabel><FormControl><Input placeholder="Nama lengkap" {...field} /></FormControl><FormMessage /></FormItem>)} />
                <FormField control={profileForm.control} name="phone" render={({ field }) => (<FormItem><FormLabel>Nomor Telepon</FormLabel><FormControl><Input placeholder="08123..." {...field} value={field.value ?? ""} /></FormControl><FormMessage /></FormItem>)} />
              </div>
              <FormField control={profileForm.control} name="address" render={({ field }) => (<FormItem><FormLabel>Alamat</FormLabel><FormControl><Textarea placeholder="Alamat lengkap" {...field} value={field.value ?? ""} /></FormControl><FormMessage /></FormItem>)} />
              <FormField control={profileForm.control} name="birthDate" render={({ field }) => (<FormItem className="flex flex-col"><FormLabel>Tanggal Lahir</FormLabel><Popover><PopoverTrigger asChild><FormControl><Button variant={"outline"} className={cn("w-full md:w-[240px] pl-3 text-left font-normal",!field.value && "text-muted-foreground")}>{field.value ? format(field.value, "PPP", { locale: localeID }) : (<span>Pilih tanggal</span>)}<CalendarIcon className="ml-auto h-4 w-4 opacity-50" /></Button></FormControl></PopoverTrigger><PopoverContent className="w-auto p-0" align="start"><Calendar mode="single" selected={field.value || undefined} onSelect={field.onChange} disabled={(date) => date > new Date() || date < new Date("1900-01-01")} initialFocus captionLayout="dropdown-buttons" fromYear={1900} toYear={new Date().getFullYear()}/></PopoverContent></Popover><FormMessage /></FormItem>)} />
              <FormField control={profileForm.control} name="bio" render={({ field }) => (<FormItem><FormLabel>Bio Singkat</FormLabel><FormControl><Textarea placeholder="Tentang Anda..." {...field} value={field.value ?? ""} /></FormControl><FormMessage /></FormItem>)} />
              <FormField control={profileForm.control} name="avatarUrl" render={({ field }) => (<FormItem><FormLabel>URL Avatar</FormLabel><FormControl><Input placeholder="https://..." {...field} value={field.value ?? ""} /></FormControl><FormDescription>URL ke gambar profil Anda.</FormDescription><FormMessage /></FormItem>)} />
              
              {appUser.role === 'siswa' && (
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <FormField control={profileForm.control} name="nis" render={({ field }) => (<FormItem><FormLabel>NIS</FormLabel><FormControl><Input placeholder="NIS" {...field} value={field.value ?? ""} /></FormControl><FormMessage /></FormItem>)} />
                  <FormField control={profileForm.control} name="kelas" render={({ field }) => (<FormItem><FormLabel>Kelas</FormLabel><FormControl><Input placeholder="Kelas" {...field} value={field.value ?? ""} /></FormControl><FormMessage /></FormItem>)} />
                </div>
              )}
              {(appUser.role === 'guru' || appUser.role === 'admin' || appUser.role === 'pimpinan') && (
                <FormField control={profileForm.control} name="nip" render={({ field }) => (<FormItem><FormLabel>NIP</FormLabel><FormControl><Input placeholder="NIP" {...field} value={field.value ?? ""}/></FormControl><FormMessage /></FormItem>)} />
              )}
              {appUser.role === 'guru' && (
                <FormField control={profileForm.control} name="mataPelajaran" render={({ field }) => (<FormItem><FormLabel>Mata Pelajaran Diampu</FormLabel><FormControl><Input placeholder="Mapel" {...field} value={field.value ?? ""} /></FormControl><FormMessage /></FormItem>)} />
              )}

              <Button type="submit" disabled={isProfileSubmitting || authLoading} className="w-full md:w-auto">
                {(isProfileSubmitting || authLoading) && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                Simpan Perubahan Profil
              </Button>
            </form>
          </Form>
        </CardContent>
      </Card>

      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle>Ubah Kata Sandi</CardTitle>
          <CardDescription>Perbarui kata sandi akun Firebase Anda.</CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...passwordForm}>
            <form onSubmit={passwordForm.handleSubmit(onPasswordSubmit)} className="space-y-6">
              <FormField control={passwordForm.control} name="newPassword" render={({ field }) => (<FormItem><FormLabel>Kata Sandi Baru</FormLabel><FormControl><Input type="password" placeholder="••••••••" {...field} /></FormControl><FormMessage /></FormItem>)} />
              <FormField control={passwordForm.control} name="confirmNewPassword" render={({ field }) => (<FormItem><FormLabel>Konfirmasi Kata Sandi Baru</FormLabel><FormControl><Input type="password" placeholder="••••••••" {...field} /></FormControl><FormMessage /></FormItem>)} />
              <Button type="submit" disabled={isPasswordSubmitting || authLoading} className="w-full md:w-auto">
                {(isPasswordSubmitting || authLoading) && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                Ubah Kata Sandi
              </Button>
            </form>
          </Form>
        </CardContent>
      </Card>
    </div>
  );
}
